<template>
  <div class="profile-wrapper">

      <yd-navbar title="个人信息">
            <router-link to="/setting" slot="left">
                <yd-navbar-back-icon>后退</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
      <h1 class="bigtitle"></h1>
      
    <yd-cell-group title="招式">

        <skills></skills>
     
    </yd-cell-group>
      <yd-cell-group title="江湖名号">
          <yd-cell-item>
            <span slot="left"> </span>
            <yd-input slot="right" type="text" required max="20" placeholder="你的名号" v-model="userInfo.username"></yd-input>
          </yd-cell-item>
      </yd-cell-group>


      <yd-cell-group title="你现实生活的职业">
          <yd-cell-item>
                <yd-input slot="right" type="text" required max="20" placeholder="" ></yd-input>
          </yd-cell-item>
      </yd-cell-group>

     

    

    <yd-button-group>
        <yd-button size="large" @click.native="updateUserInfo" type="primary">保存</yd-button>
    </yd-button-group>

  </div>
</template>

<script>
import Vqiniu from '@/components/vqiniu'
import {APIuser,APIupdateuserinfo} from '../../api/api'
import cookie from '../../static/js/cookie'
import skills from '@/page/skills/skills'

import {Input,CellGroup,CellItem} from 'vue-ydui'
import {mapState} from 'vuex'
export default {
    components:{
        skills,
        Vqiniu,
        CellGroup,
        CellItem,
        Input
    },
    data () {
        return {
            userInfo:{
                username:'',
                profile:[],
            }
        }
    },
    computed:{
        ...mapState(['userimg']),
    },
    methods: {
        
        getUserinfo () {
            this.userInfo.username = cookie.getCookie('username')
            APIuser(
                this.userInfo.username
            ).then( res => {
                this.userInfo = res.data
            })
        },
        updateUserInfo() {
            console.log(this.userimg)
            this.userInfo.profile  = this.userimg.join(',')
            APIupdateuserinfo(
                this.userInfo
            ).then( res => {
                this.res
            }).catch()
        },
        
    },
    created () {
        this.getUserinfo()
    },
    activated () {
        this.userimg = null
    }
}
</script>
<style lang="stylus" scoped>
.headimg
    width 100%
    height 200px
    .bigone
        width 60%
        background #dddddd
        height 220px
        display inline-block
    .smallone
        width 30%
        height 220px
        display inline-block
        .one
            height 100px
            margin-bottom 10px
            background #dddddd

</style>





